*{margin:0;padding:0}body{font:14px/1.3 \\5FAE\8F6F\96C5\9ED1;margin:20px}#create,#remove{margin:10px;width:100px;height:30px;line-height:30px;border:none;font-size:14px;background:#ccc;cursor:pointer;outline:none;border-radius:5px}#create:hover,#remove:hover{background:#e3e3e3}.m-note{position:absolute;left:20px;top:70px;width:200px;min-height:276px;max-height:452px;background-color:#c4e5ff;-webkit-box-shadow:0 5px 10px rgba(0,0,0,.5);box-shadow:0 5px 10px rgba(0,0,0,.5)}.m-note .u-edit{box-sizing:border-box;width:200px;min-height:250px;max-height:400px;padding:10px;outline:none;overflow-x:hidden;overflow-y:auto}.m-note:hover .u-close{display:block}.m-note .u-bar{height:26px;background:#0179d7;cursor:move}.m-note .u-close{width:30px;height:30px;position:absolute;left:-15px;top:-15px;background:url(data:image/png;base64,) no-repeat;display:none;cursor:pointer}.m-note .m-time{line-height:26px;height:26px;padding:0 10px;background-color:#c4e5ff}